.menu-item {
    min-width: 30%;
    height: 300px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    margin: 0 7.5px 15px;  
    overflow: hidden;
    // &.large{
    //   height: 380px;
    // }
    &:first-child {
      margin-right: 7.5px;
    }
    // &:nth-last-child(2){
    //   height: 380px;
    // }
    // &:last-child {
    //   margin-left: 7.5px;
    //   height: 380px;
    // }
    @media screen and (max-width: 800px) {
      height: 200px !important;

    }

    &:hover {
      cursor: pointer;
    
      & .background-image {
        transform: scale(1.1);
        transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
      }
    
      & .content {
        opacity: 0.9;
      }
    }

    .background-image{
      width: 100%;
      height: 100%;
      background-size: cover;
      background-position: center;
    }
    .content {
      height:   90px;
      padding: 0 25px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border: 1px solid black;
      background-color: white;
      opacity: 0.7;
      position: absolute;
      @media screen and (max-width: 800px) {
        height: 64px;
        padding: 0px 15px;  
      }
      .title {
        font-weight: bold;
        margin-bottom: 6px;
        font-size: 22px;
        color: #4a4a4a;
        margin: 0;
        @media screen and (max-width: 800px) {
          font-size: 20px;  
        }
      }
  
      .subtitle {
        font-weight: lighter;
        font-size: 16px;
        @media screen and (max-width: 800px) {
          font-size: 12px;  
        }
      }
    }
  }